import React, { useCallback, useRef, useState } from "react"
import { FormHandles } from "@unform/core"
import { Form } from "@unform/web"
import { Row, Col } from "react-bootstrap"
import * as Yup from "yup"
import { navigateTo } from "gatsby"

import { translate } from "../../translate"
import { Container, Button } from "./styles"
import Input from "../Input"
import Textarea from "../Textarea"
import getValidationErrors from "../../../../utils/getValidationErrors"
import CheckboxInput from "../Checkbox"
import { sendEmail } from "../../../../services/form-service"
import FileInput from "../InputFile"

interface ICheckboxOption {
	id: string
	value: string
	label: string
}

const FormBudget: React.FC = () => {
	const formRef = useRef<FormHandles>(null)
	const [loading, setLoading] = useState(false)

	const checkboxBudge: ICheckboxOption[] = [
		{
			id: "BudgeOp1",
			value: "Projetos de desenvolvimento de Sistema da Qualidade",
			label: translate(
				"form.Projetos de desenvolvimento de Sistema da Qualidade"
			),
		},
		{
			id: "BudgeOp2",
			value: "Projetos de adequações à Certificações ISO",
			label: translate("form.Projetos de adequações à Certificações ISO"),
		},
		{
			id: "BudgeOp3",
			value: "Tradução de Documentos Técnicos",
			label: translate("form.Tradução de Documentos Técnicos"),
		},
		{
			id: "BudgeOp4",
			value: "Locação de Mão de Obra especializada na Área da Qualidade",
			label: translate(
				"form.Locação de Mão de Obra especializada na Área da Qualidade"
			),
		},
		{
			id: "BudgeOp5",
			value: "Projetos laboratoriais e LTA",
			label: translate("form.Projetos laboratoriais e LTA"),
		},
		{
			id: "BudgeOp6",
			value: "Validações (Sistemas, Processos e Limpeza)",
			label: translate("form.Validações (Sistemas, Processos e Limpeza)"),
		},
		{
			id: "BudgeOp7",
			value: "Qualificação de fornecedores",
			label: translate("form.Qualificação de fornecedores"),
		},
		{
			id: "BudgeOp8",
			value: "Treinamentos 'in Company'",
			label: translate("form.Treinamentos 'In Company'"),
		},
		{
			id: "BudgeOp9",
			value: "Softwares Siemens",
			label: translate("form.Softwares Siemens"),
		},
		{
			id: "BudgeOp10",
			value: "GAP Analysis",
			label: translate("form.Gap"),
		},
	]

	const checkboxOptionsContact: ICheckboxOption[] = [
		{
			id: "contactEmail",
			value: "Contato por e-mail",
			label: translate(`form.Gostaria de receber contato por`) + " e-mail",
		},
		{
			id: "contactTelefone",
			value: "Contato por telefone",
			label: translate(`form.Gostaria de receber contato por`) + " telefone",
		},
		{
			id: "contactWhatsapp",
			value: "Contato por WhatsApp",
			label: translate(`form.Gostaria de receber contato por`) + " WhatsApp",
		},
	]

	const checkboxIAgree: ICheckboxOption[] = [
		{
			id: "concordoFormulario",
			value:
				"Eu aceito receber e-mails com noticias, conteúdos e contatos da Kivalita Consulting",
			label: translate("form.i agree form"),
		},
	]

	const handleSubmit = useCallback(async data => {
		try {
			formRef.current?.setErrors({})

			const schema = Yup.object().shape({
				nome: Yup.string().required("Nome obrigatório"),
				cargo: Yup.string().required("Cargo obrigatória"),
				email: Yup.string()
					.required("E-mail obrigatório")
					.email("Digite um e-mail válido"),
				empresa: Yup.string().required("Empresa obrigatória"),
				empresaEndereco: Yup.string().required("Endereço obrigatório"),
				empresaCidade: Yup.string().required("Cidade obrigatório"),
				localizacaoServico: Yup.string().required("Localização obrigatória"),
				interesseOrcamento: Yup.string().required("Selecione 1 ou + itens"),
				mensagem: Yup.string().required("Mensagem obrigatória"),
				concordoFormulario: Yup.string().required("Campo obrigatório"),
			})

			await schema.validate(data, { abortEarly: false })

			setLoading(true)

			await sendEmail(data)

			navigateTo("/thank-you")
		} catch (err) {
			setLoading(false)
			if (err instanceof Yup.ValidationError) {
				const errors = getValidationErrors(err)
				formRef.current?.setErrors(errors)

				return
			}

			navigateTo("/thank-you")
		}
	}, [])

	return (
		<Form ref={formRef} onSubmit={handleSubmit}>
			<Container className="containerForm">
				<Row>
					<Col xs="12" md="12" lg="4">
						<label>{translate(`form.Nome Completo`)}</label>
						<Input name="nome" />
					</Col>
					<Col xs="12" md="6" lg="4">
						<label>{translate(`form.Cargo / Profissão`)}</label>
						<Input name="cargo" />
					</Col>
					<Col xs="12" md="12" lg="4">
						<label>{translate(`form.E-mail`)}</label>
						<Input type="email" name="email" />
					</Col>
				</Row>

				<Row>
					<Col xs="12" md="6" lg="4">
						<label>{translate(`form.Empresa`)}</label>
						<Input name="empresa" />
					</Col>
					<Col xs="12" md="6" lg="4">
						<label>{translate(`form.Endereço da Empresa`)}</label>
						<Input name="empresaEndereco" />
					</Col>
					<Col xs="12" md="6" lg="4">
						<label>{translate(`form.Cidade`)}</label>
						<Input name="empresaCidade" />
					</Col>
				</Row>

				<Row>
					<Col xs="12" md="6" lg="4">
						<label>{translate(`form.Celular (Opcional)`)}</label>
						<Input name="celularOpcional" />
					</Col>

					<Col xs="12" md="6" lg="8">
						<label>{translate(`form.Local de Realização do Serviço`)}</label>
						<Input name="localizacaoServico" />
					</Col>
				</Row>

				<Row>
					<Col xs="12">
						<p>{translate("form.budget")}</p>
						<CheckboxInput
							name="interesseOrcamento"
							options={checkboxBudge}
						></CheckboxInput>
					</Col>
				</Row>

				<Row>
					<Col xs="12">
						<label>{translate(`form.message`)}</label>
						<Textarea name="mensagem" />
					</Col>
				</Row>

				<Row>
					<Col xs="12">
						<label style={{ width: "100%" }}>{translate(`form.upload`)}</label>
						<FileInput name="arquivo" />
					</Col>
				</Row>

				<Row>
					<Col xs="12">
						<p>{translate("form.way")}</p>
						<CheckboxInput
							name="tipoContato"
							options={checkboxOptionsContact}
						></CheckboxInput>
					</Col>
				</Row>

				<Row>
					<Col xs="12">
						<CheckboxInput
							name="concordoFormulario"
							options={checkboxIAgree}
						></CheckboxInput>
					</Col>
				</Row>

				<Button type="submit">
					{loading
						? translate("form.Enviando")
						: translate("form.Enviar Contato")}
				</Button>
			</Container>
		</Form>
	)
}

export default FormBudget
