import React from "react"

import BudgetComponent from "../components/pages/Budget"
import Layout from "../components/Layout"
import SEO from "../components/utils/SEO"
import { translate } from "../components/utils/translate"
import { IBreadCrumbOption } from "../interfaces"

const Budget = () => {
	const breadCrumbList: IBreadCrumbOption[] = [
		{
			url: "/",
			name: "Home",
			isActive: true,
		},
		{
			url: "budget",
			name: translate("breadCrumb.budget"),
			isActive: false,
		},
	]

	return (
		<Layout breadCrumbList={breadCrumbList}>
			<SEO
				title="Orçamento"
				description="Estamos à disposição. Se você tiver alguma dúvida quiser solicitar um orçamento, fale conosco! Deixe seu contato que retornaremos o quanto antes."
			></SEO>
			<BudgetComponent />
		</Layout>
	)
}

export default Budget
