import React from "react"

import Banner from "../../utils/Banner"
import Intro from "../../utils/IntroductionContent"
import FormBudget from "../../utils/Form/FormBudget"
import { translate } from "../../utils/translate"
import BookCall from "../../utils/BookCall"
import { Container } from "react-bootstrap"

export default function Budget() {
	return (
		<div>
			<Banner titlePage={translate("banner.budget")} />
			<Intro
				title={translate("introduction.budget.title")}
				subtitle={translate("introduction.budget.subTitle")}
				content={translate("introduction.budget.content")}
			/>
			<FormBudget />

			<Container>
				<BookCall />
			</Container>
		</div>
	)
}
